.driver-container {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: rgba(230, 230, 230, 0.16) 0px 10px 36px 0px, rgba(255, 255, 255, 0.06) 0px 0px 0px 1px;
  border-radius: 5px;
  padding: 20px;
  position: relative;
  overflow: hidden;

  .edit-box {
    z-index: 100;
    border-radius: 10px;
    position: absolute;
    right: 38px;
    top: 0;
    width: 45%;
    height: 100%;
    background-color: #c9c9c9;
    box-shadow: rgba(128, 128, 128, 0.25) 0px 0px 30px, rgba(211, 211, 211, 0.12) 0px -12px 30px, rgba(202, 202, 202, 0.29) 0px 4px 6px, rgba(216, 216, 216, 0.38) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    padding: 10px 10px 15px 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    transition: 0.4s;
    .main-box {
      width: 100%;
      height: 100%;
      display: flex;
      overflow: auto;
      .main-sides {
        padding: 10px;
        width: 50%;

        select {
          padding-left: 20px;
          margin-top: 10px;
          width: 100%;
          height: 30px;
          border-radius: 5px;
          box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
          border: 0.1px solid #000000 !important;

          option {
            p {
              .car-info {
                margin-right: 10px;
              }
            }
          }

        }

        input {
          padding-left: 20px;
          margin-top: 6px;
          width: 100%;
          height: 30px;
          border-radius: 5px;
          box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
          border: 0.1px solid #000000 !important;

          &:focus {
            outline: none;
          }
        }

        label {
          margin-top: 15px;
          font-size: 18px;
          font-weight: bold;
        }
      }
    }

    .title {
      text-align: center;
      font-weight: bold;
      color: black;
      font-size: 18px;
    }

    .buttons {
      width: 100%;
      display: flex;
      justify-content: space-around;
      margin-top: 20px;

      .cancel-btn {
        font-weight: bold;
        cursor: pointer;
        border: 2px solid #323232;
        padding: 2px 10px 2px 10px;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .edit-btn {
        font-weight: bold;
        cursor: pointer;
        padding: 2px 10px 2px 10px;
        background-color: black;
        border-radius: 5px;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }

  .edit-box-hide {
    right: -50%;
  }

  .search-box {
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 20px;
    width: 100%;
    height: 8%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #232323;

    .inputs {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 30%;
      height: 35px;
      box-shadow: rgb(255, 255, 255) 0px 0px 2px;
      border-radius: 10px;
      overflow: hidden;


      input {
        width: 85%;
        height: 100%;
        background-color: transparent;
        border: none;
        color: white;
        padding-left: 20px;

        &:focus {
          outline: none;
        }

        &::placeholder {
          color: #d2d2d2;
        }
      }

      .serach-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 15%;
        height: 100%;

        img {
          width: 50%;
        }
      }
    }

    .add-driver {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(255, 255, 255, .8);
      padding: 3px 10px 3px 10px;
      color: black;
      border-radius: 10px;
      font-weight: bold;
      cursor: pointer;
      transition: 0.3s;

      img {
        width: 30px;
        margin-left: 10px;

        &:nth-child(2) {
          width: 20px;
        }
      }

      &:hover {
        box-shadow: rgba(226, 226, 226, 0.5) 0px 0px 12px;
      }
    }
  }

  .table-content {
    width: 100%;
    height: 84%;
    overflow-y: auto;
    margin: 10px auto 0 auto;
    padding: 0 5px 10px 5px;
    border-radius: 5px;

    table {
      box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
      width: 100%;
      border-collapse: collapse;
      border-radius: 5px;

      thead {
        font-size: 14px;
        position: sticky;
        top: -2px;

        th {
          color: white;
          padding: 5px;
          border: 1px solid #575757;
          text-align: center;
          background-color: #272727;
          font-weight: normal;
        }
      }

      tbody {
        padding-top: 100px;

        tr {
          td {
            color: white;
            border: 1px solid #575757;
            padding: 10px;
            font-size: 13px;

            div {
              width: 25px;
              height: 25px;
              margin: 0 auto;

              img {
                cursor: pointer;
                width: 100%;
                height: 100%;
              }
            }
          }
        }

      }
    }
  }

  .pagination {
    margin: 10px auto 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;

    .prev {
      width: 22px;
      height: 22px;

      img {
        cursor: pointer;
        width: 100%;
        height: 100%;
      }
    }

    .items {
      cursor: pointer;
      margin-left: 3px;
      margin-right: 3px;
      font-weight: 400;
      color: white;
      font-size: 14px;
      padding-left: 6px;
      padding-right: 6px;
    }

    .active {
      background-color: rgba(255, 255, 255, 0.1);
      border: 1px solid #636363;
      border-radius: 40px;
      font-weight: 600;
    }

    .next {
      cursor: pointer;
      width: 22px;
      height: 22px;

      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  .view-docs {
    z-index: 200;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);

    .for-image {
      padding: 10px;
      width: 60%;
      height: 90%;
      background-color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      border-radius: 10px;
      box-shadow: rgb(128, 128, 128) 0px 0px 28px, rgba(90, 90, 90, 0.73) 0px 0px 10px;

      .cancel-btn {
        padding-left: 10px;
        padding-right: 10px;
        display: flex;
        justify-content: space-between;
        width: 100%;

        img {
          cursor: pointer;
          width: 30px;

          &:nth-child(2) {
            width: 25px;
            height: 25px;
          }
        }
      }

      .for-img {
        margin-top: 20px;
        width: 100%;
        height: 88%;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
          background-color: transparent;
        }
      }
    }
  }

  .hide {
    display: none;
  }
}

.modal {
  .modal-dialog {
    width: 100%;

    .modal-content {
      width: 100%;

      .modal-header {
        .edit {
          font-size: 20px !important;
          font-weight: bold;
          color: black;
        }
      }

      .modal-body {
        display: flex !important;
        color: black;
        padding: 10px;
        overflow-y: auto;
        .main-box {
          width: 100%;
          height: 100%;
          display: flex;

          .main-sides {
            width: 50%;
            padding: 10px;

            select {
              padding-left: 20px;
              margin-top: 7px;
              width: 100%;
              height: 30px;
              border-radius: 5px;
              box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
              border: 0.1px solid #000000 !important;

              option {
                p {
                  .car-info {
                    margin-right: 10px;
                  }
                }
              }

            }

            input {
              padding-left: 20px;
              margin-top: 7px;
              width: 100%;
              height: 30px;
              border-radius: 5px;
              box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
              border: 0.1px solid #000000 !important;

              &:focus {
                outline: none;
              }
            }

            label {
              margin-top: 10px;
              font-size: 16px;
              font-weight: bold;
            }
          }
        }
      }

      .modal-footer {
        padding: 5px;
        button {
          padding: 5px 10px 5px 10px;
          border-radius: 5px;
          background-color: black;
          border: none;
          color: white;
        }
      }
    }
  }
}

@media (min-width: 1390px) {
  .driver-container {
    .edit-box {
      top: 15%;
      height: 72%;
    }
  }
}

