.admin-home {
  width: 100%;
  height: 100vh;
  background-image: url("./images/bg4.jpg");
  background-size: 100%;
  display: flex;

  .left-box {
    width: 18%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.75);
    padding-top: 15px;

    .logo {
      margin: 0 auto;
      width: 200px;

      img {
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
    }

    .line {
      margin: 15px auto 0 auto;
      width: 75%;
      height: 2px;
      background: white;
      background: linear-gradient(90deg, rgba(249, 246, 255, 0) 0%, rgba(249, 246, 255, 1) 50%, rgba(249, 246, 255, 0) 100%);
      border-radius: 50%;
    }

    .header {
      margin-top: 25px;
      width: 100%;
      height: 40px;
      display: flex;
      border-bottom: 1px solid white;

      .item {
        font-size: 18px;
        font-weight: 500;
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        cursor: pointer;
        transition: .4s;
      }

      .active {
        box-shadow: rgb(135, 135, 135) 0px 10px 60px -10px inset, rgb(208, 208, 208) 0px 10px 20px -18px inset;
      }
    }

    .admin-navbar {
      width: 100%;
      height: 69vh;
      overflow-y: scroll;
      padding-left: 3px;

      &::-webkit-scrollbar {
        width: 8px;
        border-radius: 20px;
      }
      &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: #515151;
        outline: none;
      }

      a {
        text-decoration: none;
      }

      .nav-item {
        font-size: 16px;
        padding-left: 25px;
        display: flex;
        align-items: center;
        width: 100%;
        height: 40px;
        color: white;
        margin-top: 10px;
        cursor: pointer;
        border-left: 4px solid transparent;
        transition: .3s linear;

        img {
          margin-right: 14px;
          width: 30px;

        }
      }

      .active {
        border-left: 4px solid white;
        background-color: rgba(255, 255, 255, 0.1);
      }

    }
  }

  .right-box {
    width: 82%;
    height: 100%;

    .top-box {
      width: 100%;
      height: 80px;
      background-color: rgba(0, 0, 0, 0.75);
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 40px;
      padding-right: 40px;

      .statistic-box{
        width: 80%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .statistic-item {
          color: white;

          .title {
            font-weight: bold;
          }

          .count {
            margin-top: 5px;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 2px;
            background-color: rgba(255, 255, 255, 0.1);
            color: #bfbfbf;
            border-radius: 3px;
          }

          .count-box{
            display: flex;
            align-items: center;
            justify-content: center;
            .count {
              display: flex;
              align-items: center;
              justify-content: center;
              padding: 4px;
              background-color: rgba(255, 255, 255, 0.1);
              color: #bfbfbf;
              border-radius: 3px;
              span{
                margin-left: 5px;
              }
            }
            img{
              cursor: pointer;
              width: 25px;
              margin-left: 10px;
            }
          }

        }
      }

      .icons {
        width: 15%;
        display: flex;
        .language-box {
          width: 70px;
          display: flex;
          align-items: center;
          justify-content: space-around;

          .lng-item {
            color: white;
            cursor: pointer;
          }

          .active {
            border-bottom: 1px solid white;
          }
        }

        .exit {
          margin-left: 40px;
          width: 30px;
          height: 30px;
          cursor: pointer;

          img {
            width: 100%;
            height: 100%;
          }
        }

      }

    }

    .bottom-box {
      padding: 8px;
      width: 100%;
      height: calc(100vh - 80px);
    }
  }
}

@media (max-width : 768px ){
  .admin-home {
    width: 100%;
    height: 100vh;
    background-image: url("./images/bg4.jpg");
    background-size: 100%;
    display: flex;

    .left-box {

      .logo {
        width: 70px;
      }

      .line {
        margin: 10px auto 0 auto;
        width: 100%;
      }

      .header {
        height: auto;
        display: block;
        .item {
          font-size: 16px;
          width: 100%;
          height: 30px;
        }
      }

      .admin-navbar {

        .nav-item {
          font-size: 16px;
          padding-left: 5px;
          justify-content: center;
          height: 40px;
          span{
            display: none;
          }
          img {
            margin-right: 0px;
            width: 25px;
          }
        }

        .active {
          border-left: 4px solid white;
          border-right: 4px solid white;
          background-color: rgba(255, 255, 255, 0.1);
        }
      }
    }

    .right-box {
      width: 82%;
      height: 100%;

      .top-box {
        width: 100%;
        height: 50px;

        .statistic-box{
          display: none;
        }

        .icons {
          width: 100%;
          display: flex;
          justify-content: space-between;
          .language-box {

            .lng-item {
              margin-right: 20px;
            }
          }

          .exit {
            width: 20px;
            height: 20px;
            img {
              width: 100%;
              height: 100%;
            }
          }

        }

      }

      .bottom-box {
        padding: 4px;
        width: 100%;
        height: calc(100vh - 50px);
      }
    }
  }
}