.about-app-wrapper{
  width: 100%;
  background-image: url("./bg-images/bg4.jpg");
  background-size: 100%;

  .mirror-sloy{
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
    .header-side{
      width: 80%;
      margin: 40px auto;
      display: flex;
      justify-content: space-around;
      align-items: center;
      .header-item{
        width: 34%;
        border-radius: 20px;
        border: 1px solid #575757;
        padding: 8px;
        transition: 0.4s;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        .image{
          margin-right: 5px;
          img{
            width: 60px;
          }
        }
        .name{
          font-size: 14px;
          text-align: center;
          color: white;
        }
        &:hover{
          box-shadow: rgb(215, 215, 215) 0px 6px 24px 0px, rgb(255, 255, 255) 0px 0px 0px 1px;
        }
      }
    }

    .header-active{
      background-color: rgba(255,255,255,0.15);
      box-shadow: rgb(215, 215, 215) 0px 6px 24px 0px, rgb(255, 255, 255) 0px 0px 0px 1px;
    }

    .video-box{
      width: 80%;
      height: 550px;
      margin: 80px auto;
      border: 10px double white;
      padding: 15px;
      box-shadow: rgb(215, 215, 215) 0px 6px 24px 0px, rgb(255, 255, 255) 0px 0px 0px 1px;
      iframe{
        width: 100%;
        height: 100%;
      }
    }
  }

}

@media (max-width: 768px) {
  .about-app-wrapper{
    background-image: none;
    background-color: #333232;
    .mirror-sloy{
      width: 100%;
      .header-side{
        display: block;
        .header-item{
          margin-top: 30px;
          width: 100%;
          .image{
            margin-right: 5px;
            img{
              width: 60px;
            }
          }
          .name{
            font-size: 14px;
            text-align: center;
            color: white;
          }
          &:hover{
            box-shadow: rgb(215, 215, 215) 0px 6px 24px 0px, rgb(255, 255, 255) 0px 0px 0px 1px;
          }
        }
      }

      .header-active{
        background-color: rgba(255,255,255,0.15);
        box-shadow: rgb(215, 215, 215) 0px 6px 24px 0px, rgb(255, 255, 255) 0px 0px 0px 1px;
      }

      .video-box{
        width: 100%;
        height: 280px;
        margin: 80px auto;
        border: 5px double white;
        padding: 15px;
        box-shadow: rgb(215, 215, 215) 0px 6px 24px 0px, rgb(255, 255, 255) 0px 0px 0px 1px;
        iframe{
          width: 100%;
          height: 100%;
        }
      }
    }

  }

}