.agreement-wrapper {
  width: 100%;
  background-image: url("./bg-images/bg4.jpg");
  background-size: 100%;

  .mirror-sloy {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);

    .title {
      font-size: 36px;
      text-align: center;
      font-weight: bold;
      color: white;

    }

    .list-agreement {
      margin-top: 40px;
      margin-bottom: 100px;
      width: 65%;
      padding-bottom: 15px;
      padding-top: 15px;
      box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;

      .header-side {
        padding-left: 40px;
        padding-right: 40px;
        width: 100%;
        height: 50px;
        background-color: rgba(255, 255, 255, 0.5);
        display: flex;
        justify-content: space-between;
        align-items: center;

        .paper-clip {
          margin-top: -47px;
          width: 50px;
          height: 50px;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .icon {
          margin-top: 20px;
          cursor: pointer;
          width: 30px;
          height: 30px;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;

          p {
            font-size: 14px;
          }

          img {
            transition: 0.4s;
            width: 100%;
            height: 100%;

            &:hover {
              transform: scale(1.17);
            }
          }
        }
      }

      .body-side {
        background-color: rgba(255, 255, 255, 0.1);
        padding: 40px;
        width: 100%;
        height: 800px;
        overflow-y: auto;

        .title {
          font-size: 20px;
        }

        .text {
          text-align: justify;
          margin-top: 20px;
          font-size: 18px;
          color: white;

        }
      }
    }
  }
}


@media (max-width: 768px) {
  .agreement-wrapper {
    background: none;
    background-color: #333232;
    .mirror-sloy {
      .title {
        font-size: 28px;
        margin-top: 30px;
      }

      .list-agreement {
        margin-top: 40px;
        margin-bottom: 100px;
        width: 95%;

        .header-side {
          padding-left: 30px;
          padding-right: 30px;

          .paper-clip {
            margin-top: -47px;
            width: 50px;
            height: 50px;

            img {
              width: 100%;
              height: 100%;
            }
          }

          .icon {
            cursor: pointer;
            width: 32px;
            height: 32px;

            img {
              transition: 0.4s;
              width: 100%;
              height: 100%;

              &:hover {
                transform: scale(1.17);
              }
            }
          }
        }

        .body-side {
          background-color: rgba(255, 255, 255, 0.1);
          padding: 20px;
        }
      }
    }
  }
}